import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component'
import {FormControl, Validators, FormGroup} from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
//import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';


@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./category-list.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})
export class CategoryListComponent implements OnInit,AfterViewInit {
	 @ViewChild(MatSort) sort: MatSort;
	 @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	
	 @ViewChild(ListComponent)
	 private list: ListComponent;
	  
	 listHide: boolean = true;
	addHide: boolean = false;
	animateAdd: string = 'out';
	animateList: string = 'in';
	  
	categoryForm: FormGroup;
	  
	opened: boolean = false;
	catId = 0;  
	pageValue: string ="1";
	sortField: string ="";
	sortDirection: string ="";
	length = 10;
    pageSize = 5;
    pageSizeOptions = [5, 10, 25, 100];
	pageIndex: number=1;
	ajaxData : CategoryData[];
	category = new CategoryData();

	public branchs = [];
	  
    constructor(private _script: ScriptLoaderService, 	private http: HttpClient,) {
    }

    ngOnInit() {
		
		this.categoryForm = new FormGroup({	
		
		});
		
		this.listHide = false;
		this.animateAdd='out';
    	this.animateList='in';
		
  		this.list.listUrl="http://localhost:8060/axelacrm/service/categories-list";

	}
	  
	    toggleAnimation(){
		if(this.animateAdd == 'out' || this.animateList == 'in'){
			this.listHide = true;
			this.addHide = false;
			if(this.catId == 0){
				this.populateDropDownData();
			}else{
				this.populateData();
			}
			this.animateList = 'out';
			this.animateAdd = 'in';
		}else{
			this.addHide = true;
			this.listHide = false;
			this.categoryForm.reset();
			this.list.getData();
			this.animateAdd= 'out';
			this.animateList = 'in';
		}
	  }
	  
	  populateDropDownData(){
		 this.opened = true; 
	  }

		populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<CategoryData>(sessionStorage.getItem('requestUrl')+'/axelacrm/slaes/category-data',
							 			JSON.stringify({ catId :this.catId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.category = data['populateData'][0];
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 },
															 () => {
			  														this.populateDropDownData();								
															 });
	 }


     ngAfterViewInit(){
		if(!this.addHide){
			this.addHide = true;
		}else{
			this.addHide = false;
		}
	}
	  
	  onSubmit() {
		  alert("submit");
		  if(!this.categoryForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.catId == 0){
			this.category['requestType'] = 'add';
		}else{
			this.category['requestType'] = 'update';
			this.category['catId'] = this.catId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/category',
									JSON.stringify(this.category),
						{ headers, withCredentials: true }).subscribe(data => {
			 											this.toggleAnimation();
			 											this.categoryForm.reset();
			 											this.list.getData();
														});
		  }
	  }
	  
	  listClickListener(listParam: any[]){
		if(listParam[0] == 'updatecCategory'){
			this.catId = listParam[1];
			this.toggleAnimation();
		}
		 
	}
}

export class CategoryData {
	catId :string;
	catName :string;
	catEntryId :string;
	catEntryDate :string;
	catModifiedId :string;
	catModifiedDate :string;
	
}




